<template>
    <div v-if="readyState">
        <NavII @menuStateChange="menuStatus=!menuStatus"></NavII>
        <ArticleTiledBackground></ArticleTiledBackground>
        <div class="content">
            <transition name="bg" appear>
                <div class="top-photo" :style="'background-image: url('+ aboutMeDate.illustration +')'">
                    <div :class="[{'yuki-AboutMe-illustration-change':menuStatus || !scrollChange}]"
                         class="yuki-AboutMe-illustration-title">
                        <span id="yuki-AboutMe-illustration-title" @click="goIllustrationPage"></span>
                    </div>
                    <div :class="[{'yuki-AboutMe-illustration-change':menuStatus || !scrollChange}]"
                         class="yuki-AboutMe-illustration-artist">
    <span id="yuki-AboutMe-illustration-artist" @click.prevent="goIllustratorPage">Illustrator :
    <span id="yuki-AboutMe-illustration-artist-name" style="font-family: DFM;">Bison</span></span>
                    </div>
                </div>
            </transition>
            <div class="title selectNG">
                <div class="title-position">
                    <transition name="title" appear>
                        <div>
                            <div style="transition: 0.6s ease-in-out"
                                 :class="[{'yuki-AboutMe-title-change':!scrollChange}]"><span id="title"></span>
                            </div>
                        </div>
                    </transition>
                </div>
                <transition name="titleInfo" appear>
                    <div class="title-info">
                        <div style="transition: 0.6s ease-in-out"
                             :class="[{'yuki-AboutMe-titleInfo-change':!scrollChange}]"><span>About Me</span></div>
                    </div>
                </transition>
            </div>
            <transition name="normal" appear>
                <div>
                    <div class="AboutMe-center-part">
                        <div class="yuki-aboutMe-avatar">
                            <div class="yuki-aboutMe-avatar-border selectNG">
                                <img class="yuki-aboutMe-avatar-img" @mousedown.prevent="()=>{return false;}"
                                     src="https://cdn.jsdelivr.net/gh/yukibako/ImgHosting/Yukikaze/yukibako.jpg">
                            </div>
                        </div>
                        <div class="yuki-aboutMe-sign selectNG">
                            <div style="padding:18px 0"><span>One heart One flower One world...</span></div>
                        </div>
                        <div class="yuki-aboutMe-main">
                            <div class="yuki-aboutMe-main-section">
                                Hi,YUKIBAKO de~su!...4080v0...
                            </div>
                            <div class="yuki-aboutMe-main-section">
                                java,<span class="bracket">()</span>...
                            </div>
                            <div class="yuki-aboutMe-main-section">
                                <span class="bracket">(Q.Q..)</span><span
                                    class="bracket">()</span>...
                                /...
                            </div>
                            <div class="yuki-aboutMe-main-section">
                                ,~~<span class="bracket">()</span>Sakurairo
                                <span class="bracket">(<a target="_blank" href="https://2heng.xin">Mashiro</a><a
                                        target="_blank" href="https://asuhe.jp">Hitomi</a>~)</span>
                                <a target="_blank" href="https://suikouflower.com">Suikouflower</a>...
                                <a target="_blank" href="http://www.chiso.co.jp/">CHISO</a><span
                                    class="bracket">(I am so vegetable...)</span>
                            </div>
                            <div class="yuki-aboutMe-main-section">
                                springboot+vueShiro,ES()......<span
                                    class="bracket">(~)</span>
                                vueCRUD...<span class="bracket">(emmm..~)</span>
                            </div>
                            <div class="yuki-aboutMe-main-section">
                                :<a target="_blank"
                                                   href="https://weibo.com/hexieshizhede4H?from=myfollow_all"></a><span
                                    class="bracket">()</span>
                                P<span class="bracket">(...)</span>,~<span
                                    class="bracket">(...)</span>,...
                                <!--                (,)...-->
                            </div>
                            <div class="yuki-aboutMe-button-position">
                                <el-button @click="showEmail" id="yuki-email-button" type="info" icon="el-icon-message"
                                           plain circle></el-button>
                            </div>
                        </div>
                        <div style="padding-top: 80px" class="end selectNG"><span id="end">''~</span></div>
                    </div>
                    <Footer :no-line="false"></Footer>
                </div>
            </transition>
        </div>
        <div class="flandre-position" @click="returnTop" :class="[{'flandre-disappear':menuStatus || scrollChange}]">
            <img @mousedown.prevent="()=>{return false;}" class="flandre-img"
                 src="https://cdn.jsdelivr.net/gh/yukibako/ImgHosting/Illustration/flandre.png">
        </div>
    </div>
</template>

<script>
    import NavII from "../components/NavII";
    import ArticleTiledBackground from "../components/Article/ArticleTiledBackground";
    import Footer from "../components/Footer";


    export default {
        name: "AboutMe",
        components: {Footer, ArticleTiledBackground, NavII},
        data: function () {
            return {
                readyState: false,
                hasError: false,
                scrollChange: true,
                menuStatus: false,
                aboutMeDate: {
                    illustration: 'https://cdn.jsdelivr.net/gh/yukibako/ImgHosting/Illustration/71303816_p0.jpg',
                }
            }
        },
        methods: {
            showEmail() {
                this.$notify({
                    dangerouslyUseHTMLString: true,
                    title: '..',
                    message: '<span style="color: #b698c7;text-shadow: 4px 4px 10px rgb(180, 253, 212);line-height: 25px;letter-spacing: 1px"><span style="color: #ff957a;text-shadow: 4px 4px 12px rgb(254, 255, 162);">wp993769581@live.cn</span>...<br>,...<br> _(:)_</span>'
                });
            },
            goIllustrationPage() {
                window.open('https://www.pixiv.net/artworks/71303816', '_blank');
            },
            goIllustratorPage() {
                window.open('https://www.pixiv.net/users/333556', '_blank');
            },
            scrollJudge() {
                let scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
                this.scrollChange = scrollTop < window.screen.availHeight * 0.3;
            },
            returnTop() {
                window.scroll({top: 0, left: 0, behavior: 'smooth'});
            },
            illustrationLoad(key) {
                let newImg = new Image();
                let f = () => {
                    newImg.src = this.aboutMeDate.illustration;
                    newImg.onload = () => {
                        this.$emit('hasLoaded');
                        this.readyState = true;
                    };
                    //
                    newImg.onerror = () => {
                        this.hasError = true;
                        this.$emit('Error');
                    };
                };
                if (key)
                    setTimeout(() => f(), 2100);
                else
                    f();
            }
        },
        created() {
            window.addEventListener('scroll', () => this.scrollJudge());
            document.title=' | YUKIBAKO';
        },
        mounted() {
            if (!this.$store.state.hadEntered)
                window.onload = () => {
                    this.illustrationLoad(false);
                    this.$store.state.hadEntered = true;
                };
            else
                this.illustrationLoad(true);
        },
        destroyed() {
            this.$emit('viewChange');
        },
    }
</script>

<style scoped src="../assets/css/aboutMe/yuki-aboutMe.css"></style>
<style scoped src="../assets/css/yuki-animation.css"></style>
<style scoped>

    .yuki-aboutMe-button-position {
        text-align: right;
        position: relative;
        width: 97%;
        top: -10px;
    }

    #yuki-email-button {
        background: rgba(239, 227, 255, 0.45);
        color: #6666ff;
        border-color: #ffc084;
        transition: 0.35s ease-in-out;
    }

    #yuki-email-button:hover {
        color: #e0625d;
        /*background: rgba(205, 255, 218, 0.45);*/
        border-color: #d35cff;
    }

    .yuki-AboutMe-illustration-change {
        opacity: 0;
    }

    .yuki-AboutMe-title-change {
        opacity: 0;
        filter: blur(3px);
        transform: translateY(60px);
    }

    .yuki-AboutMe-titleInfo-change {
        opacity: 0;
        filter: blur(3px);
        transform: translateX(-30px);
    }

    .end {
        width: 78%;
        margin-left: auto;
        margin-right: auto;
        /*margin-top: 80px;*/
        /*margin-bottom: 30px;*/
        padding-top: 40px;
        padding-bottom: 80px;
        text-align: right;
    }

    #end {
        font-size: 25px;
        letter-spacing: 5px;
        color: #f7bac5;
        text-shadow: 4px 4px 10px rgb(255, 253, 160);
    }

</style>